function isValidInput(key) {
    if (key >= 48 && key <= 57) {
        // 
        return true;
    }

    if (key >= 65 && key <= 90) {
        // 
        return true;
    }

    if (key === 32 // 
        || key > 188 // 
    ) {
        return true;
    }

    return false;
}

export default {
    isValidInput
};



