import React from 'react';
import { Menu, SubMenu, MenuItem, MenuItemGroup } from './MacMenu';

import Clock from './components/Clock';

export default () => {
    return (
        <Menu>
            <div className="pull-left">
                <SubMenu
                    text={
                        <i
                            className="fa fa-apple"
                            aria-hidden="true"
                            style={{ fontSize: 16 }}
                        ></i>
                    }
                >
                    <MenuItemGroup>
                        <MenuItem></MenuItem>
                    </MenuItemGroup>
                    <MenuItemGroup>
                        <MenuItem>...</MenuItem>
                        <MenuItem>App Store...</MenuItem>
                    </MenuItemGroup>
                    <MenuItemGroup>
                        <MenuItem>...</MenuItem>
                    </MenuItemGroup>
                </SubMenu>
                <SubMenu
                    text="Finder"
                >
                    <MenuItemGroup>
                        <MenuItem> Finder</MenuItem>
                    </MenuItemGroup>
                    <MenuItemGroup>
                        <MenuItem>...</MenuItem>
                        <MenuItem>...</MenuItem>
                    </MenuItemGroup>
                    <MenuItemGroup>
                        <MenuItem>...</MenuItem>
                    </MenuItemGroup>
                </SubMenu>
                <SubMenu
                    text=""
                >
                    <MenuItemGroup>
                        <MenuItem> Finder </MenuItem>
                        <MenuItem></MenuItem>
                    </MenuItemGroup>
                </SubMenu>
            </div>
            <div className="pull-right">
                <SubMenu text={<Clock />}>

                </SubMenu>
                <SubMenu
                    text={
                        <i className="fa fa-list-ul" aria-hidden="true"></i>
                    }
                >
                </SubMenu>
            </div>
        </Menu>
    )
}



